import React, { Component } from 'react';
import { Row, Col, Form, Checkbox } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from '../../../components/common/index';
import { getTrainTypes } from '../../../services/trainType';
import { requiredRule } from '../../../utils/validateRules';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Học kỳ 1'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019'
        },
        {
          id: '2',
          label: '2019-2020'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1'
        },
        {
          id: '2',
          label: '2'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDoituonghocphi = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Ngoài ngân sách'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getKhoahoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khóa 2018'
        },
        {
          id: '2',
          label: 'Khóa 2019'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
      console.log(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const nganh2 = getFieldValue('nganh2');
    const hoclaicaithien = getFieldValue('hoclaicaithien');
    const duocmien = getFieldValue('duocmien');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Hệ đào tạo">
              {getFieldDecorator('hedaotao', {})(<SelectAjax service={getTrainTypes} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('namhoc', {})(<SelectAjax service={this.getNamhoc} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item {...formItemLayout} label="Khóa học">
              {getFieldDecorator('khoahoc', {})(<SelectAjax service={this.getKhoahoc} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Học kì" required>
              {getFieldDecorator('hocki', { rules: [requiredRule], })(<SelectAjax service={this.getSemester} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item {...formItemLayout} label="Đợt">
              {getFieldDecorator('dot', {})(<SelectAjax service={this.getDot} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Đối tượng học phí">
              {getFieldDecorator('doituonghocphi', {})(<SelectAjax service={this.getDoituonghocphi} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={16}>
            <Form.Item {...formItemLayout} label="Lọc theo điều kiện">
              <Row>
                <Col span={5} style={{ textAlign: 'center' }}>
                  {getFieldDecorator('nganh2', {})(<Checkbox checked={nganh2}>Ngành 2</Checkbox>)}
                </Col>
                <Col span={5} style={{ textAlign: 'center' }}>
                  {getFieldDecorator('hoclaicaithien', {})(<Checkbox checked={hoclaicaithien}>Học lại, cải thiện</Checkbox>)}
                </Col>
                <Col span={5} style={{ textAlign: 'center' }}>
                  {getFieldDecorator('duocmien', {})(<Checkbox checked={duocmien}>Được miễn giảm</Checkbox>)}
                </Col>
                <Col span={9} style={{ textAlign: 'right' }}>
                  <div className="btn-group">
                    <BtnSearch htmlType="submit" />
                    <BtnClear onClick={this.handleReset} />
                  </div>
                </Col>
              </Row>
            </Form.Item>
          </Col>
        </Row>
      </Form >
    );
  }
}

export default SearchBar;



