import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, Input, Checkbox, message, Button } from 'antd';
import { TableDataEditRow, CartHeadAction } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';
import { getCreditsPrice, updateCreditsPrice } from '../../../services/creditsPrice';
import { moneyToString } from '../../../utils/formatUtil';

class CreditsPrice extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Đối tượng học phí',
      dataIndex: 'doituonghocphi',
      key: 'doituonghocphi',
    },
    {
      title: 'Khóa học',
      dataIndex: 'khoahoc',
      key: 'khoahoc',
    },
    {
      title: 'Học ngành 2',
      dataIndex: 'hocnganh2',
      editable: true,
      render: (text, record) => (
        <Checkbox defaultChecked={record.hocnganh2} disabled />
      ),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('hocnganh2', {
            initialValue: record.hocnganh2,
          })(<Checkbox defaultChecked={record.hocnganh2} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Học lại',
      dataIndex: 'hoclai',
      editable: true,
      render: (text, record) => (
        <Checkbox defaultChecked={record.hoclai} disabled />
      ),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('hoclai', {
            initialValue: record.hoclai,
          })(<Checkbox defaultChecked={record.hoclai} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Được miễn',
      dataIndex: 'duocmien',
      editable: true,
      render: (text, record) => (
        <Checkbox defaultChecked={record.duocmien} disabled />
      ),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('duocmien', {
            initialValue: record.duocmien,
          })(<Checkbox defaultChecked={record.duocmien} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Đơn giá/1 tín chỉ',
      dataIndex: 'dongia1tinchi',
      key: 'dongia1tinchi',
      editable: true,
      render: text => moneyToString(text),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('dongia1tinchi', {
            initialValue: record.dongia1tinchi,
          })(<Input />)}
        </Form.Item>
      ),
    },
    {
      title: 'Bù kinh phí',
      dataIndex: 'bukinhphi',
      key: 'bukinhphi',
      editable: true,
      render: text => moneyToString(text),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('bukinhphi', {
            initialValue: record.bukinhphi,
          })(<Input />)}
        </Form.Item>
      ),
    },
    {
      title: 'Tính chất học phí',
      dataIndex: 'tinhchathocphi',
      key: 'tinhchathocphi',
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleCopyToNextYear = () => {
    message.success('Sao chép sang kì sau thành công');
  }

  handleSaveRowData = async rowData => {
    try {
      await updateCreditsPrice(rowData);
      return rowData;
    } catch (e) {
      return null;
    }
  };

  render() {
    return (
      <Card>
        <CartHeadAction>
          <Button type='primary' onClick={this.handleCopyToNextYear}>Sao chép sang kì sau</Button>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getCreditsPrice}
              columns={this.columns}
              handleSaveRowData={this.handleSaveRowData}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreditsPrice />
  </Page>
);



